import { Component, OnInit } from '@angular/core';
import {NewsStore} from "../../store/news.store";
import {NewsService} from "../../service/news.service";
import {INews} from "../../model/news.model";
import {Router} from "@angular/router";

@Component({
  selector: 'app-all-news',
  templateUrl: './all-news.page.html',
  styleUrls: ['./all-news.page.scss'],
})
export class AllNewsPage implements OnInit {
  // loading = false;
  constructor(public newsStore: NewsStore, private newsService: NewsService, private router: Router) { }

  async ngOnInit() {
    await this.newsService.getAllNews();
  }

  selectNews(news: INews) {
    this.newsStore.setSelectedNews(news);
    this.router.navigate(['/news']);
  }
}
